import { Injectable } from '@angular/core';
import {ItemsModule} from './items.module';
import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from '../shopping-list/shopping-list.service';
import { Subject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class ItemsService {
  itemsChanged = new Subject<ItemsModule[]>();

  private items: ItemsModule[]= [
    new ItemsModule(
      'Burger', 
      'Food', 
      'https://www.thespruceeats.com/thmb/OE0BKB5LZeEhi_OEjinOCxVA0Bg=/1500x1000/filters:no_upscale():max_bytes(150000):strip_icc()/juicy-baked-burgers-3052097-7-5b1054308e1b6e0036bc6cd1.jpg',
      [new Ingredient('Meat', 1), 
      new Ingredient('Bread', 2)]),
    new ItemsModule(
      'Pizza', 
      'Food', 
      'https://upload.wikimedia.org/wikipedia/commons/a/a3/Eq_it-na_pizza-margherita_sep2005_sml.jpg',
      [new Ingredient('Bread', 3), 
      new Ingredient('Cheese', 4)])
  ];

  constructor(private slService : ShoppingListService) { }

  setItems(items: ItemsModule[]) {
    this.items = items;
    this.itemsChanged.next(this.items.slice());
  }

  getItems() {
    return this.items.slice();
  }

  getItem(id: number){
    return this.items[id];
  }

  addIngredientsToShoppingList(ingredients: Ingredient[]) {
    this.slService.addIngredients(ingredients);
  }

  addItem(item: ItemsModule) {
    this.items.push(item);
    this.itemsChanged.next(this.items.slice())
  }

  updateItem(index: number, newItem: ItemsModule){
    this.items[index] = newItem;
    this.itemsChanged.next(this.items.slice())
  }

  deleteItem(index: number){
    this.items.splice(index, 1);
    this.itemsChanged.next(this.items.slice())
  }

 
}
