import { Injectable } from "@angular/core";
import { Http,Response } from "@angular/http";
import { ItemsService } from "../items/items.service";
import { ItemsModule } from "../items/items.module";
import { map } from 'rxjs/operators';
import { AuthService } from "../auth/auth.service";

@Injectable()
export class DataStorageService {
    constructor(private http: Http, private itemsService: ItemsService, private authService: AuthService) {}

    storeItems() {
        const token = this.authService.getToken()
        return this.http.put('https://ng-myweb.firebaseio.com/items.json?auth=' + token, this.itemsService.getItems());
    }

    getItems(){
        const token = this.authService.getToken()
        
        this.http.get('https://ng-myweb.firebaseio.com/items.json?auth=' + token)
        .pipe(map(
            (response: Response) => {
                const items: ItemsModule[] = response.json();
                for( let item of items) {
                    if(!item['ingredients']) {
                        item['ingredients'] = [];
                    }
                }
                return items;
            }
        ))
        .subscribe(
            (items: ItemsModule[]) => {
                this.itemsService.setItems(items);
            }
        );
    }
}