import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { FormGroup, FormControl, FormsModule, FormArray, Validators } from '@angular/forms';
import { ItemsService } from '../items.service';
import { ItemsModule } from '../items.module';

@Component({
  selector: 'app-item-edit',
  templateUrl: './item-edit.component.html',
  styleUrls: ['./item-edit.component.css']
})
export class ItemEditComponent implements OnInit {
  id:number;
  editMode = false;
  itemForm: FormGroup;

  constructor(private route: ActivatedRoute, private itemService: ItemsService, private router: Router) { }

  ngOnInit() {
    this.route.params
    .subscribe(
      (params : Params) => {
        this.id = +params['id'];
        this.editMode = params['id'] != null;
        this.initForm();
      }
    );
  }
    onSubmit() {
      // const newItem = new ItemsModule(
      //   this.itemForm.value['name'], 
      //   this.itemForm.value['description'],
      //   this.itemForm.value['imagePath'],
      //   this.itemForm.value['ingredients']
      //   );
      if(this.editMode) {
        this.itemService.updateItem(this.id, this.itemForm.value)
      }else {
        this.itemService.addItem(this.itemForm.value);
      }
      this.onCancel();
    }

    onAddIngredient() {
      (<FormArray>this.itemForm.get('ingredients')).push(
        new FormGroup({
          'name': new FormControl(null, Validators.required),
          'amount': new FormControl(null, [Validators.required, Validators.pattern(/^[1-9]+[0-9]*$/)])
        })
      )
    }

    onCancel(){
      this.router.navigate(['../'],{relativeTo: this.route});
    }

    onDeleteIngredient(index: number){
      (<FormArray>this.itemForm.get('ingredients')).removeAt(index);
    }
  

  private initForm() {
    let itemName = '';
    let itemImagePath= '';
    let itemDescription = '';
    let itemIngredients = new FormArray([]);

    if(this.editMode) {
      const item = this.itemService.getItem(this.id);
      itemName = item.name;
      itemImagePath = item.imagePath;
      itemDescription = item.description;
      if(item['ingredients']) {
        for(let ingredient of item.ingredients){
          itemIngredients.push(
            new FormGroup({
              'name': new FormControl(ingredient.name, Validators.required),
              'amount': new FormControl(ingredient.amount, [Validators.required, Validators.pattern(/^[1-9]+[0-9]*$/)])
            })
          );
        }
      }
    }
    this.itemForm = new FormGroup({
      'name': new FormControl(itemName, Validators.required),
      'imagePath': new FormControl(itemImagePath, Validators.required),
      'description': new FormControl(itemDescription, Validators.required),
      'ingredients': itemIngredients
    });
  }
  getControls(){
    return (<FormArray>this.itemForm.get('ingredients')).controls;
  }

}
